@import url("https://fonts.googleapis.com/css2?family=Song+Myung&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap");

body {
  line-height: 1.6;
  font-weight: bold;
  margin: 50px;
  background-color: rgb(207, 230, 238);
  font-family: 'Arial, sans-serif';
}

header {
  font-size: 30px;
  height: 150px;
  color: white;
  font-family: "Nanum Gothic";
  background-color: #333;
  border-bottom: 2px solid gray;
  text-align: center;
  padding: 10px;
  margin: 0;
  font-family: 'Arial, sans-serif';
}

.container {
  display: flex;
  gap: 10px;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: 'Arial, sans-serif';
}

nav {
  border-right: 2px solid red;
  margin-left: 5%;
  font-size: 20px;
  color: white;
  font-family: 'Arial, sans-serif';
}

main {
  flex: 4;
  font-size: 18px;
  margin-left: 20px;
  font-family: 'Arial, sans-serif';
}

main:hover {
  background-color: #f0f8ff;
}

article {
  margin: 10px;
  font-size: 16px;
  border-left: 2px solid red;
  padding-left: 10px;
  font-family: 'Arial, sans-serif';
}

footer {
  text-align: center;
  border-top: 2px solid gray;
  border-bottom: 2px solid gray;
  margin: 20px;
  padding: 10px;
  font-family: 'Arial, sans-serif';
}

a {
  display: inline-block;
  text-decoration: none;
  color: white;
  padding: 5px;
  font-family: 'Arial, sans-serif';
}

a:hover {
  background-color: #4169e1;
  color: white;
}

table {
  border: 2px dotted blue;
  border-collapse: collapse;
  width: 70%;
  margin: 20px;
  font-family: 'Arial, sans-serif';
}

td {
  border: 2px dotted blue;
  padding: 15px;
  font-family: 'Arial, sans-serif';
}

input[type="text"],
input[type="password"] {
  border: 2px dotted orange;
  border-radius: 4px;
  width: 30%;
  padding: 8px;
  margin-bottom: 10px;
}

input[type="password"]:focus {
  background-color: #ffff00;
}

.menu {
  width: 300px;
  color: white;
  background-color: green;
  border-radius: 8px;
  padding: 10px;
  font-size: 25px;
  font-family: 'Arial, sans-serif';
}

.menu > li {
  line-height: 40px;
}

.menu > li:hover {
  background-color: blue;
  transition-duration: 1s;
}

.menu > li:hover .submenu {
  display: block;
  transition-duration: 1s;
}

.submenu {
  display: none;
  padding-left: 10px;
  color: white;
  font-family: 'Arial, sans-serif';
}

.Career {
  font-family: 'Arial, sans-serif';
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}


.article {
  color: black;
  font-family: 'Arial, sans-serif';
}

.em1 {
  font-size: 1.2em;
  font-weight: bold;
  color: #4CAF50;
  margin-bottom: 10px;
}

.table1 {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.td1 {
  width: 20%;
  border: 2px solid black;
  padding: 15px;
  font-family: 'Arial, sans-serif';
}

.table1 th, .table1 td {
  border: 1px solid black;
  padding: 8px;
  text-align: left;
}

.table1 th {
  background-color: #4CAF50;
  color: black;
}

.table1 tr:nth-child(even) {
  background-color: white;
}

.table1 tr:hover {
  background-color: rgb(0, 201, 100);
}

.pledge1 {
  margin-top: 20px;
  font-size: 1.2em;
  font-style: italic;
  text-align: center;
  color: #333;
}
.cc {
  width: 300px;
  height: 400px;
  font-family: 'Arial, sans-serif';
}

img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.selfintro {
  font-size: 1.5em;
  border: 2px solid blue;
  margin: 20px;
  padding: 20px;
  border-radius: 8px;
  font-family: 'Arial, sans-serif';
}

.login-button {
  margin-top: 10px;
  margin-right: 10px;
  text-align: right;
}

.login-button a {
  padding: 8px 12px;
  background-color: #4CAF50;
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.login-button a:hover {
  background-color: #45a049;
}

.Career {
  max-width: 600px;
  margin: 0 auto;
}

fieldset {
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 20px;
}

legend {
  font-size: 1.2em;
  font-weight: bold;
  color: #666;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin-bottom: 10px;
}

.listItemBackground {
  background-color: #f9f9f9;
  padding: 8px;
  border-radius: 5px;
}

h1 {
  text-align: center;
  color: #333;
}

.Js {
  font-family: 'Arial, sans-serif';
  
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.Js h2 {
  color: #333;
  text-align: center;
}

.Js ul {
  list-style-type: none;
  padding: 0;
}

.Js li {
  margin-bottom: 10px;
  color: #555;
}

.Js button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 15px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.Js img {
  width: 200px;
  height: 300px;
  border-radius: 8px;
  margin: 5px;
}
